<template>
    <div>
        <!-- 头部部分 -->
        <div class="title">
            <van-nav-bar :title="$route.params.name">
                <template #left>
                    <van-icon
                        name="arrow-left"
                        size="0.67rem"
                        @click="$router.go(-1)"
                    />
                </template>

                <template #right>
                    <van-icon name="search" size="0.67rem" @click="goSearch" />
                </template>
            </van-nav-bar>
        </div>
        <!-- 图片部分 -->
        <div class="img">
            <van-image
                width="8rem"
                height="8rem"
                fit="cover"
                round
                :src="$route.params.picUrl"
            />
        </div>
        <!-- 图标 -->
        <div class="icon">
            <van-icon :name="like" @click="change1" />

            <van-icon name="share-o" />
        </div>
        <!-- 播放部件 -->
        <div class="song">
            <div class="songWord">
                <Lrc :id="$route.params.id" :currentTime="currentTime"></Lrc>
            </div>

            <audio :src="getUrl" controls autoplay ref="player"></audio>
        </div>
    </div>
</template>

<script>
import Lrc from "../Lrc/index.vue";
export default {
    name: "Player",
    data() {
        return {
            like: "like-o",
            songData: {},
            id: "",
            flag: true,
            currentTime: 0,
        };
    },
    components: { Lrc },
    computed: {
        getUrl() {
            return decodeURIComponent(
                "http://music.163.com/song/media/outer/url?id=" +
                    this.$route.params.id +
                    ".mp3"
            );
        },
    },
    beforeDestroy() {
        this.removEvenHandle();
    },
    mounted() {
        this.addEvenHandle();
    },
    methods: {
        //点赞功能
        change1() {
            this.flag = !this.flag;
            if (this.flag) {
                this.like = "like";
            } else {
                this.like = "like-o";
            }
        },
        //跳转到搜索页面
        goSearch() {
            this.$router.push({
                path: "/layout/search",
            });
        },
        // 获取音乐时长
        addEvenHandle() {
            this.$refs.player.addEventListener(
                "timeupdate",
                this.currentTimeHandle
            );
        },
        // 移出获取歌曲时间事件
        removEvenHandle() {
            this.$refs.player.removeEventListener(
                "timeupdate",
                this.currentTimeHandle
            );
        },
        currentTimeHandle() {
            this.currentTime = this.$refs.player.currentTime;
        },
    },
};
</script>

<style scoped lang="less">
// 头部部分
/deep/ .van-nav-bar__content {
    height: 1.5rem;

    .van-nav-bar__title {
        font-size: 0.5rem;
        font-weight: 600;
    }
}
// 图片部分
.img {
    margin-top: 1rem;
    display: flex;
    justify-content: center;
}
// 图标
.icon {
    margin-top: 0.5rem;
    display: flex;
    justify-content: space-around;
    font-size: 0.5rem;
}
.song {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 1.5rem;
    audio {
        width: 100%;
        margin-top: 0.9rem;
    }
    .songWord {
        text-align: center;
        padding-bottom: 0.5rem;
        font-size: 0.5rem;
        font-family: "Arial", "Microsoft YaHei", "黑体", "宋体", sans-serif;
        color: #666666;
    }
}
</style>