<template>
    <div>
        <div class="searchHeader">
            <van-nav-bar>
                <template #left>
                    <van-icon
                        name="arrow-left"
                        size="0.67rem"
                        @click="$router.go(-1)"
                    />
                </template>
            </van-nav-bar>
        </div>

        <van-search
            shape="round"
            v-model="searchValue"
            placeholder="请输入搜索关键词"
        />
        <!-- 搜索下容器 -->
        <div class="search_wrap" v-if="resultList.length === 0">
            <!-- 标题 -->
            <p class="hot_title">热门搜索</p>
            <!-- 热搜关键词容器 -->
            <div class="hot_name_wrap">
                <!-- 每个搜索关键词 -->
                <span
                    class="hot_item"
                    v-for="(obj, index) in hotArr"
                    :key="index"
                    @click="btn(obj.first)"
                    >{{ obj.first }}</span
                >
            </div>
        </div>
        <!-- 搜索结果 -->
        <div class="search_wrap" v-else>
            <!-- 标题 -->
            <p class="hot_title">最佳匹配</p>
            <router-link
                :to="`/player/${obj.id}/${obj.name}/${obj.picUrl}`"
                v-for="obj in resultList"
                :key="obj.id"
            >
                <van-cell-group>
                    <van-cell center :title="obj.name">
                        <template #right-icon>
                            <van-icon name="play-circle-o" />
                        </template>
                    </van-cell>
                </van-cell-group>
            </router-link>
        </div>
    </div>
</template>

<script>
import { newtui1, resou, searchResultList } from "@/api";
export default {
    data() {
        return {
            searchValue: "", // 搜索关键词
            hotArr: [], // 热搜关键字
            resultList: [], // 搜索结果
            timer: null, // 输入框-防抖定时器
        };
    },
    async created() {
        const res = await resou();

        this.hotArr = res.result.hots;
    },
    methods: {
        btn(str) {
            this.searchValue = str;
        },
        // 编码以便数据能正常传输
        setUrl(url) {
            if (url) {
                return encodeURIComponent(url);
            }
        },
    },
    watch: {
        searchValue(val) {
            clearTimeout(this.timer);
            if (val.length === 0) return (this.resultList = []);
            this.timer = setTimeout(async () => {
                const res1 = await searchResultList({
                    keywords: val,
                }).then((res) => {
                    this.resultList = res.result.songs;
                });
            }, 500);
        },
    },
};
</script>

<style scoped>
.searchHeader {
    position: absolute;
    top: 0;
}
/* 搜索容器的样式 */
.search_wrap {
    padding: 0.266667rem;
}

/*热门搜索文字标题样式 */
.hot_title {
    font-size: 0.32rem;
    color: #666;
}

/* 热搜词_容器 */
.hot_name_wrap {
    margin: 0.266667rem 0;
}

/* 热搜词_样式 */
.hot_item {
    display: inline-block;
    height: 0.853333rem;
    margin-right: 0.213333rem;
    margin-bottom: 0.213333rem;
    padding: 0 0.373333rem;
    font-size: 0.373333rem;
    line-height: 0.853333rem;
    color: #333;
    border-color: #d3d4da;
    border-radius: 0.853333rem;
    border: 1px solid #d3d4da;
}

/* 给单元格设置底部边框 */
.van-cell {
    border-bottom: 1px solid lightgray;
}
</style>