import request from "./request";
import axios from 'axios'
// 今日推荐
export const tuijian = params => request({ url: '/personalized?limit=6', params })
//推荐新音乐
export const newtui = () => request({ url: '/personalized/newsong?limit=10', method: 'get' })
//热搜
export const resou = () => request({ url: '/search/hot', method: 'get' })
//搜索结果 
export const searchResultList = params => request({ url: '/search', params })
//获取歌词
export const getLrc = params => request({ url: '/lyric', params })

