<template>
    <div>
        <!-- 头部 -->
        <van-nav-bar :title="activeTitle" fixed> </van-nav-bar>
        <!-- 中间部分 -->
        <div class="main">
            <keep-alive>
                <router-view></router-view>
            </keep-alive>
        </div>
        <!-- 底部 -->
        <van-tabbar route>
            <van-tabbar-item icon="home-o" replace to="/layout/home"
                >首页</van-tabbar-item
            >
            <van-tabbar-item icon="search" to="/layout/search"
                >搜索</van-tabbar-item
            >
        </van-tabbar>
    </div>
</template>

<script>
export default {
    data() {
        return {
            activeTitle: this.$route.meta.title,
        };
    },

    watch: {
        $route() {
            this.activeTitle = this.$route.meta.title;
        },
    },
};
</script>

<style scoped>
.main {
    padding-top: 46px;
    padding-bottom: 50px;
}
</style>