<template>
    <div class="lrcContainer">
        <div class="lrc" ref="lrc">
            <p
                class="lrc-p"
                :class="{
                    active:
                        currentTime > allKyes[index] &&
                        currentTime < allKyes[index + 1],
                }"
                v-for="(item, key, index) in LrcData"
                :key="index"
            >
                <span>{{ item }}{{ scollLrc(index) }} </span>
            </p>
        </div>
    </div>
</template>

<script>
import { getLrc } from "@/api";

export default {
    name: "LRC",
    data() {
        return {
            LrcData: "",
            allKyes: [],
        };
    },
    props: {
        id: {
            type: [String, Number],
            required: true,
        },
        currentTime: {
            type: Number,
            required: 0,
        },
    },

    mounted() {
        // 获取歌词
        getLrc({ id: this.id }).then((res) => {
            this.LrcData = res.lrc.lyric;
            // 切割歌词
            this.filterLRC(res.lrc.lyric);
        });
    },
    methods: {
        //过滤歌词
        filterLRC(values) {
            if (!values) return;
            var lrc = {};
            var lyrics = values.split("\n");
            // 匹配规则
            var reg = /\[\d*:\d*(\.|:)\d*]/g;
            for (var i = 0; i < lyrics.length; i++) {
                var timerRegArr = lyrics[i].match(reg);
                if (!timerRegArr) continue;
                //获取歌词
                var content = lyrics[i].replace(timerRegArr, "");
                //获取时间
                var t = timerRegArr[0];
                var min = parseInt(t.match(/\[\d*/i).toString().slice(1));
                var sec = parseInt(t.match(/\:\d*/i).toString().slice(1));
                var time = min * 60 + sec;
                lrc[time] = content;
            }
            this.LrcData = lrc;
            this.getAllKeys(lrc);
        },
        //每句歌词对应的key
        getAllKeys(lrcArr) {
            for (var key in lrcArr) {
                this.allKyes.push(key);
            }
        },
        // 歌词滚动
        scollLrc(index) {
            if (
                this.currentTime > this.allKyes[index] &&
                this.currentTime < this.allKyes[index + 1]
            ) {
                this.$refs.lrc.style.top = -(30 * (index - 2)) + "px";
            }
        },
    },
};
</script>

<style>
.active {
    color: red;
}
.lrcContainer {
    width: 100%;
    height: 150px;
    overflow: scroll;
    position: relative;
}
.lrc {
    width: 100%;
    position: absolute;
    top: 0;
}
.lrc-p {
    height: 30px;
    line-height: 30px;
}
.up {
    margin-top: -30px;
}
</style>